import React from "react";
import itemTable from './excel/item_table'
import withStyles from "@material-ui/core/styles/withStyles";
import PropTypes from "prop-types";

const rarityColor = [
    {color:'rgb(160,160,160)'},
    {color:'rgb(217,229,59)'},
    {color:'rgb(1,178,248)'},
    {color:'rgb(180,148,182)'},
    {color:'rgb(253,201,4)'},
    {color:'rgb(253,201,4)'},
]

const iconStyle = theme=>({
    icon:{
        backgroundImage:props=>`url('./img/items/${props.id}.png')`,
        backgroundSize: 'auto 100%',
        backgroundPosition:'center center',
        backgroundRepeat:'no-repeat',
        margin:'auto',
        height:'87.5%',
        width:'87.5%',
    },
    root:{
        backgroundImage:props=>`url(${require('./img/items/bg/'+String(props.rarity)+'.png')})`,
        display:'flex',
        backgroundSize: 'auto 100%',
        backgroundPosition:'center center',
        backgroundRepeat:'no-repeat',
        height:'50px',
        width:'50px',
        position:'relative',
    },
    // rBgColor:{
    //     backgroundColor:props=>rarityColor[props.rarity].color,
    // },
});

function itemIcon(props) {
    const {classes,id, rarity, ...other}=props;
    return (
            <div className={classes.root} {...other}>
                <div className={classes.icon}/>
            </div>
    )
}
itemIcon.propTypes = {
    id: PropTypes.string.isRequired,
    rarity: PropTypes.number.isRequired,
    count: PropTypes.number,
};
const RawItemIcon = withStyles(iconStyle)(itemIcon);
function ItemIcon(props) {
    const {id,...other}=props
    return(
        <RawItemIcon
            id={itemTable.items[props.id].iconId}
            rarity={itemTable.items[props.id].rarity}
            {...other}
        />
    )
};


function itemGet(id,name) {
    return itemTable.items[id][name];
}

function itemCountStyled(id,count) {
    return (<span style={rarityColor[itemTable.items[id].rarity]}>{itemTable.items[id].name + ' x'+count.toString()}</span>)
}

export {ItemIcon,itemCountStyled,itemGet};

