import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import PersistentDrawerLeft from './demo';
import Chips from './chip'


import * as serviceWorker from './serviceWorker';

ReactDOM.render(<PersistentDrawerLeft />, document.getElementById('root'));
ReactDOM.render(<Chips/>, document.getElementById('app'));
// ReactDOM.render(<PaperSheet/>, document.getElementById('test'));
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
function scrollTo() {
    let hash = this.props.location.hash.replace('#', '');
    if (hash) {
        let node = ReactDOM.findDOMNode(this.achor[hash]);
        if (node) {
            node.scrollIntoView();
        }
    }
}


serviceWorker.unregister();
